@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;700&display=swap");
::selection {
  background-color: #e11d48;
  color: #f8fdf5;
}

html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.line-clamp-1 {
  box-orient: vertical;
  display: -webkit-box;
  line-clamp: 1;
  overflow: hidden;
}

.line-clamp-2 {
  box-orient: vertical;
  display: -webkit-box;
  line-clamp: 2;
  overflow: hidden;
}

.line-clamp-3 {
  box-orient: vertical;
  display: -webkit-box;
  line-clamp: 3;
  overflow: hidden;
}

main {
  min-height: calc(100vh - 100px);
}

#heroSection {
  min-height: 90vh;
}
#heroSection .hero_image {
  height: 200px;
}
@media (min-width: 1200px) {
  #heroSection .hero_image {
    height: 500px;
  }
}

#featureSection .card img {
  height: 36px;
  object-fit: contain;
  width: 36px;
}
@media (min-width: 992px) {
  #featureSection .card img {
    height: 44px;
    width: 44px;
  }
}

#aboutSection .aboutTeamSection .card {
  align-items: center;
}
#aboutSection .aboutTeamSection .card-img-top {
  border-radius: 50%;
  height: 56px;
  object-fit: contain;
  width: 56px;
}
#aboutSection .aboutTeamSection .card-body {
  padding: 0;
}

#clientSection .card {
  align-items: center;
}
#clientSection .card-img-top {
  height: 100px;
  object-fit: contain;
}

#blogPage .card-img-top {
  height: 200px;
  object-fit: cover;
}
@media (min-width: 768px) {
  #blogPage .card-img-top {
    height: 225px;
  }
}
@media (min-width: 1200px) {
  #blogPage .card-img-top {
    height: 250px;
  }
}

